import { getf, getc } from "skandha";
import { Insertion } from "../Insertion";
import { Selection } from "../Selection";
export function selectionIsInsertedOnDragAndDrop(facet, dropPosition) {
    const ctr = getc(facet);
    getf(Insertion, ctr).insertItems(Object.assign(Object.assign({}, dropPosition), { payload: getf(Selection, ctr).items }));
}
